html {
  height: 100%;
  width: 100%;
}

body {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  color: #424242;
  font-family: 'Rubik', sans-serif;
  height: 100%;
  margin: 0;
  padding: 0;
  width: 100%;
}

#root {
  height: 100%;
  width: 100%;
}

code {
  font-family: source-code-pro, 'Courier New', monospace;
}

a,
a:hover,
input:focus,
button:focus {
  text-decoration: none;
}

button {
  background: none;
  border: none;
  cursor: pointer;
  font-family: 'Rubik', sans-serif;
}

button:focus {
  outline: none;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5 {
  margin: 0;
}

